import vars from '../_vars';

if (vars.$marketingCard) {
  let cardCounter = 0;
  let cardDelay = 4000;

  const dataCard = [{
      title: 'Faux shearling double-breasted coat',
      where: 'London, Great Britain',
    },
    {
      title: 'Double-breasted coat',
      where: 'Milan, Italy',
    },
    {
      title: 'Faux shearling',
      where: 'Moscow, Russia',
    }
  ];

  const closeMarketingCard = () => {
    vars.$marketingCard.classList.remove('marketing-card--visible');
  }

  const changeMarketingCardData = () => {
    vars.$marketingCard.classList.remove('marketing-card--visible');

    setTimeout(() => {
      vars.$marketingCard.classList.add('marketing-card--visible');
    }, cardDelay - 2000);

    const stringTitle = `${dataCard[cardCounter].title}`;
    const stringWhere = `15 minutes ago ${dataCard[cardCounter].where}`;

    setTimeout(() => {
      vars.$marketingCard.querySelector('.marketing-card__title').textContent = stringTitle;
      vars.$marketingCard.querySelector('.marketing-card__when-from').textContent = stringWhere;
    }, 900);

    cardCounter++;

    if (cardCounter == dataCard.length) {
      cardCounter = 0;
    }
  }

  changeMarketingCardData();

  setInterval(changeMarketingCardData, cardDelay);

  vars.$marketingCard.addEventListener('click', (e) => {
    if (e.target.classList.contains('marketing-card__close')) {
      closeMarketingCard();
    }
  });
}


